"use client";
import { Box, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";

const SemiCircleProgress = ({ principal, interest }) => {
  const [progressValue, setProgressValue] = useState(0);
  const total = principal + interest;
  const principalPercentage = (principal / total) * 100;

  const calculateOffset = (percent) => {
    const circumference = 314.16;
    return ((100 - percent) / 100) * circumference;
  };

  useEffect(() => {
    setProgressValue(principalPercentage);
  }, [principalPercentage]);

  return (
    <Box sx={{ position: "relative", width: "100%", height: "250px" }}>
      <svg
        viewBox="0 0 200 200"
        style={{
          transform: "rotate(-180deg)",
          width: "100%",
          height: "100%",
        }}
      >
        <circle
          cx="100"
          cy="100"
          r="50"
          fill="none"
          stroke="#E6E6FA"
          strokeWidth="20"
          strokeDasharray="314.16"
          strokeDashoffset="157.08" 
          transform="rotate(90 100)"
        />
        <circle
          cx="100"
          cy="100"
          r="50"
          fill="none"
          stroke="#4ade80"
          strokeWidth="20"
          strokeDasharray="314.16"
          strokeDashoffset={157.08 + calculateOffset(progressValue)}
          transform="rotate(90 100 100)"
          style={{
            transition: "stroke-dashoffset 0.5s ease-in-out",
          }}
        />
      </svg>
    </Box>
  );
};

